body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.Landing h1 {
  font-family: 'Pacifico', cursive;
  font-size: 4rem;
  color: rgb(244, 41, 95);
  
}
.Event {
  display: flex;
  flex-flow: column nowrap;
}
.friendField {
  margin: 5px auto;
  padding: 5px;
  width: 500px;
  height: 70px;
}

.deleteBtn {
  position: relative;
  top: -60px;
  left: 480px;
  margin: 0;
  padding: 0;
  width: 30px;
  height: 30px;
}
.Backdrop {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.4);
  z-index: 100;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
}

.Backdrop h1 {
  font-family: 'Pacifico', cursive;
  font-size: 4rem;
  text-align: center;
  color: white;
  width: 400px;
  margin: 0 auto;
}
.Shuffler {
  margin: 0 auto;
  width: 80%;
  height: 100vh;
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-content: center;
}

.inputFriend {
  display: inline;
  margin: 0 5px;
}
.submitBtn {
  width: 400px;
  height: 90px;
  font-family: 'Pacifico', cursive;
  font-size: 2rem;
  color: white;
  outline: none;
  border-radius: 20px;
  border: 2px solid rgb(244, 41, 95);
  border-bottom: 2px solid rgba(255,255,255,0.4);
  background-color: rgb(244, 41, 95);
  box-shadow: 0px 8px  rgb(198, 0, 49);
}

.submitBtn:hover {
  margin-top: 2px;
  box-shadow: 0px 6px  rgb(198, 0, 49);
}

.submitBtn:active {
  margin-top: 8px;
  box-shadow: 0px 0px  rgb(198, 0, 49);
}

/* Steps Buttons */
#prevStep, 
#nextStep {
  position: fixed;
  top: 50%;
}

#prevStep {
  left: 20px;
}

#nextStep {
  right: 20px;
}
